import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { tap } from 'rxjs/operators';
import { NativeStorage } from '@ionic-native/native-storage/ngx';
import { EnvService } from './env.service';
import { User } from '../models/user';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
@Injectable({
  providedIn: 'root'
})

export class AuthService {
  arrayemail = [];
  arrayMobileNo = [];
  isLoggedIn = false;
  token: any;
  lengData: number;
  userName: any;

  constructor(
    private http: HttpClient,
    private storage: NativeStorage,
    private env: EnvService,

  ) { }
  login(pinNumber: string) {
    const headers = new HttpHeaders().set('Content-Type', 'application/json');
    const params = new HttpParams().set('pin', pinNumber);
    return this.http.get(this.env.API_URL + 'users/login', { headers, params }).pipe(
      tap(data => {
        this.isLoggedIn = true;
        this.userName = data[0].name;
        return this.userName;
      }),
    );
  }

  forgotpwd(email: string, mobileNo: string) {
    const headers = new HttpHeaders().set('Content-Type', 'application/json');
    const params = new HttpParams().set('email', email).set('mobileNo', mobileNo);
    return this.http.get(this.env.API_URL + 'users/isUser', { headers, params });
  }
  logout() {
    const headers = new HttpHeaders({
      'Authorization': this.token['token_type'] + ' ' + this.token['access_token']
    });
    return this.http.get(this.env.API_URL + 'logout', { headers })
      .pipe(
        tap(data => {
          this.storage.remove('token');
          this.isLoggedIn = false;
          delete this.token;
          return data;
        })
      );
  }
  user() {
    const headers = new HttpHeaders({
      'Authorization': this.token['token_type'] + ' ' + this.token['access_token']
    });
    return this.http.get<User>(this.env.API_URL + 'auth/user', { headers })
      .pipe(
        tap(user => {
          return user;
        })
      );
  }
  getToken() {
    return this.storage.getItem('token').then(
      data => {
        this.token = data;
        if (this.token != null) {
          this.isLoggedIn = true;
        } else {
          this.isLoggedIn = false;
        }
      },
      error => {
        this.token = null;
        this.isLoggedIn = false;
      }
    );
  }
}
