import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './guard/auth.guard';
const routes: Routes = [
  {
    path: '',
    redirectTo: 'landing',
    pathMatch: 'full'
  },
  { path: 'landing', loadChildren: './pages/landing/landing.module#LandingPageModule' },  
  { path: 'login', loadChildren: './pages/auth/login/login.module#LoginPageModule' },
  { path: 'register', loadChildren: './pages/auth/register/register.module#RegisterPageModule' },
  
  { path: 'dashboard', loadChildren: './pages/dashboard/dashboard.module#DashboardPageModule', canActivate: [AuthGuard] },
  { path: 'home', loadChildren: './home/home.module#HomePageModule', canActivate: [AuthGuard] },
  { path: 'list', loadChildren: './list/list.module#ListPageModule', canActivate: [AuthGuard] },
  { path: 'forgotpwd', loadChildren: './pages/auth/forgotpwd/forgotpwd.module#ForgotpwdPageModule' },
  { path: 'requestregister', loadChildren: './pages/auth/requestregister/requestregister.module#RequestregisterPageModule' },
  { path: 'enterpin', loadChildren: './pages/auth/enterpin/enterpin.module#EnterpinPageModule' },
  { path: 'enterpin2', loadChildren: './pages/auth/enterpin2/enterpin2.module#Enterpin2PageModule' },
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}